.container.contact__container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  gap: 12%;
}

.contact__options {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.data__span {
  display: none;
}

.d-none {
  display: none;
}

.contact__option {
  background: var(--color-bg-variant);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  transition: var(--transition);
}

.contact__option:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
}

.contact__option-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}
.btn-kirim {
  border-radius: 12px;
  background: linear-gradient(145deg, --color-primary-variant, #086089);
  box-shadow: 17px 17px 34px transparent, -17px -17px 34px --color-bg-variant;
}

.contact__option a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}

.alert {
  background-color: green;
  height: max-content;
  padding: 1vh;
  border-radius: 12px;
  width: fit-content;
}

form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

button:disabled,
button[disabled] {
  border: 2px solid #110a7a;
  background-color: #110a7a;
  color: #666666;
}

input,
textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: transparent;
  border: 2px solid var(--color-primary-variant);
  resize: none;
  color: var(--color-white);
}

@media screen and (max-width: 1024px) {
  .container.contact__container {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  .contact__option {
    font-size: 20px !important;
  }
}
@media screen and (max-width: 600px) {
  .container.contact__container {
    grid-template-columns: 1fr;
    width: var(--container-width-sm);
  }
  .contact__option {
    font-size: 20px !important;
  }
}
